            <div class="tree-node-content">
              <span>{{ title }}</span>
              <div class="operation-buttons">
                <a-button type="link" size="small" @click.stop="handleAdd(treeKey)">
                  <a-icon type="plus" />
                </a-button>
                <a-button type="link" size="small" @click.stop="handleEdit(treeKey)">
                  <a-icon type="edit" />
                </a-button>
                <a-button type="link" size="small" @click.stop="handleDelete(treeKey)">
                  <a-icon type="delete" />
                </a-button>
              </div>
            </div>
          </template>
        </a-tree>
      </div>
      
    </div>

      <div style="flex:1;height: 100%;overflow-y: auto;overflow-x: hidden;padding: 0 20px;
      box-sizing: border-box;">
        <!-- Main Form Content -->
        <div >
          <!-- 标题和保存按钮  头部固定-->
           <div style="margin-bottom: 10px;background-color: #fff;display: flex;justify-content: space-between;align-items: center;position: sticky;top:0;z-index: 2;">
            <span style="font-size: 20px;font-weight: bold;">新菜单</span>
            <a-button type="primary" @click="handleSubmit">保存</a-button>
           </div>
          <a-form  :label-col="{ span: 6 }" layout="horizontal" :wrapper-col="{ span: 16 }">
            <a-row :gutter="24">
              <a-col :span="12">
                <a-form-item label="菜单名称" :required="true" >
                  <a-input
                    v-model="formData.menuName"
                    placeholder="请输入菜单名称"
                  />
                </a-form-item>
              </a-col>
              <a-col :span="12">
                <a-form-item label="上级菜单">
                  <a-select
                    v-model="formData.parentMenu"
                    placeholder="请选择上级菜单"
                  >
                    <a-select-option value="权限管理">权限管理</a-select-option>
                  </a-select>
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24">
              <a-col :span="12">
                <a-form-item label="菜单图标">
                  <a-input-search
                    v-model="formData.menuIcon"
                    placeholder="点击后面按钮选择图标"
                  >
                    <!-- 后置插槽 -->
                      <a-button slot="enterButton" type="primary" @click="handleSelectIcon"
                        >选择图标</a-button
                      >
                  </a-input-search>
                </a-form-item>
              </a-col>
              <a-col :span="12">
                <a-form-item label="菜单类型" :required="true">
                  <a-radio-group v-model="formData.menuType">
                    <a-radio-button value="menu">菜单</a-radio-button>
                    <a-radio-button value="button">按钮</a-radio-button>
                    <a-radio-button value="external">外链</a-radio-button>
                    <a-radio-button value="iframe">Iframe</a-radio-button>
                  </a-radio-group>
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24">
              <a-col :span="12">
                <a-form-item label="路由地址" :required="true">
                  <a-input
                    v-model="formData.routePath"
                    placeholder="页面访问地址，以 '/' 开头"
                  />
                </a-form-item>
              </a-col>
              <a-col :span="12">
                <a-form-item label="路由重定向">
                  <a-input
                    v-model="formData.redirectPath"
                    placeholder="利用 vue-router 跳转其他路由地址，以 '/' 开头"
                  />
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24">
              <a-col :span="24">
                <a-form-item
                  label="视图地址"
                  :label-col="{ span: 3 }"
                  :wrapper-col="{ span: 20 }"
                >
                  <a-input v-model="formData.viewPath" placeholder="视图地址，请指向模块或插件下的views目录文件">
                    <template slot="addonBefore">
                      <a-select
                        v-model="formData.viewPathPrefix"
                        style="width: 120px"
                      >
                        <a-select-option value="src/modules/"
                          >src/modules/</a-select-option
                        >
                      </a-select>
                    </template>
                    <template slot="addonAfter">
                      <a-select
                        v-model="formData.viewPathSuffix"
                        style="width: 80px"
                      >
                        <a-select-option value=".vue">.vue</a-select-option>
                      </a-select>
                    </template>
                  </a-input>
                  
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24">
              <a-col :span="12">
                <a-form-item label="国际化">
                  <a-input
                    v-model="formData.i18nKey"
                    placeholder="菜单标题国际化"
                  />
                </a-form-item>
              </a-col>
              <a-col :span="12">
                <a-form-item label="排序">
                  <a-input-number
                    v-model="formData.sortOrder"
                    :min="0"
                    :precision="0"
                    style="width: 100%"
                  />
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24" >
              <a-col :span="8" >
                <a-form-item label="是否启用" :wrapper-col="{ span: 10 }">
                  <a-switch v-model="formData.isEnabled" />
                </a-form-item>
              </a-col>
              <a-col :span="8">
                <a-form-item label="是否隐藏" :wrapper-col="{ span: 10 }">
                  <a-switch v-model="formData.isHidden" />
                </a-form-item>
              </a-col>
              <a-col :span="8">
                <a-form-item label="是否缓存" :wrapper-col="{ span: 10 }">
                  <a-switch v-model="formData.isCached" :checked="true" />
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24">
              <a-col :span="8" >
                <a-form-item label="版权显示" :wrapper-col="{ span: 10 }">
                  <a-switch v-model="formData.showCopyright" />
                </a-form-item>
              </a-col>
              <a-col :span="8">
                <a-form-item label="面包屑显示" :wrapper-col="{ span: 10 }">
                  <a-switch v-model="formData.showBreadcrumb" :checked="true" />
                </a-form-item>
              </a-col>
              <a-col :span="8">
                <a-form-item label="Tab是否固定" :wrapper-col="{ span: 10 }">
                  <a-switch v-model="formData.isTabFixed" />
                </a-form-item>
              </a-col>
            </a-row>

            <a-row :gutter="24">
              <a-col :span="24">
                <a-form-item
                  label="备注"
                  :label-col="{ span: 3 }"
                  :wrapper-col="{ span: 20 }"
                >
                  <a-textarea
                    v-model="formData.remark"
                    :rows="4"
                    :max-length="255"
                  />
                </a-form-item>
              </a-col>
            </a-row>

            <a-row v-if="formData.menuType === 'menu'">
              <a-col :span="24">
                <a-form-item
                  label="按钮权限"
                  :label-col="{ span: 3 }"
                  :wrapper-col="{ span: 21 }"
                >
                  <a-table
                    :columns="buttonColumns"
                    :data-source="formData.buttons"
                    :pagination="false"
                    bordered
                  >
                    <template slot="name" slot-scope="text, record, index">
                      <a-input v-model="record.name" placeholder="按钮名称" />
                    </template>
                    <template
                      slot="identifier"
                      slot-scope="text, record, index"
                    >
                      <a-input
                        v-model="record.identifier"
                        placeholder="按钮标识"
                      />
                    </template>
                    <template slot="i18n" slot-scope="text, record, index">
                      <a-input v-model="record.i18n" placeholder="按钮国际化" />
                    </template>
                    <template slot="operation" slot-scope="text, record, index">
                      <a-button
                        type="danger"
                        size="small"
                        @click="removeButton(index)"
                        >删除</a-button
                      >
                    </template>
                  </a-table>
                  <a-button type="dashed" block @click="addButton">
                    <a-icon type="plus" /> 新增一个
                  </a-button>
                </a-form-item>
              </a-col>
            </a-row>
          </a-form>

         
        </div>
      </div>
  </div>
</template>

<script>
import {
  DatabaseOutlined,
  PaperClipOutlined,
  ClockCircleOutlined,
  LockOutlined,
} from "@ant-design/icons-vue"; //Ensure correct antdesign vue2 compatible version

export default {
  components: {
    DatabaseOutlined, //Register as components, important for Vue2 Options API
    PaperClipOutlined,
    ClockCircleOutlined,
    LockOutlined,
    // No need to register PlusOutlined, we're using <a-icon type="plus">
  },
  data() {
    return {
      collapsed: false,
      selectedKeys: ["data-center"],
      expandedKeys: [
        "attachment-management",
        "log-management",
        "permission-management",
      ],
      searchValue: "",
      expandAllByDefault: true,
      formData: {
        menuName: "",
        parentMenu: "权限管理",
        menuIcon: "",
        menuType: "menu",
        routePath: "",
        viewPath: "",
        viewPathPrefix: "src/modules/",
        viewPathSuffix: ".vue",
        redirectPath: "",
        i18nKey: "",
        isEnabled: true,
        isHidden: false,
        isCached: true,
        showCopyright: false,
        showBreadcrumb: true,
        isTabFixed: false,
        sortOrder: 0,
        remark: "",
        buttons: [],
      },
      buttonColumns: [
        {
          title: "按钮名称",
          dataIndex: "name",
          scopedSlots: { customRender: "name" },
        },
        {
          title: "按钮标识",
          dataIndex: "identifier",
          scopedSlots: { customRender: "identifier" },
        },
        {
          title: "按钮国际化",
          dataIndex: "i18n",
          scopedSlots: { customRender: "i18n" },
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" },
        },
      ],
      // 添加模拟菜单数据
      mockMenuData: {
        menu: {
          menuName: "新建菜单",
          parentMenu: "",
          menuIcon: "dashboard",
          menuType: "menu",
          routePath: "/demo",
          viewPath: "views/demo",
          viewPathPrefix: "src/modules/",
          viewPathSuffix: ".vue",
          redirectPath: "",
          i18nKey: "menu.demo",
          isEnabled: true,
          isHidden: false,
          isCached: true,
          showCopyright: true,
          showBreadcrumb: true,
          isTabFixed: false,
          sortOrder: 0,
          remark: "这是一个示例菜单"
        }
      }
    };
  },
  computed: {
    treeData() {
      // Get routes from store
      const routes = this.$store.state.routers;

      
      // Transform routes to tree structure
      return this.transformRoutesToTree(routes);
    }
  },
  methods: {
    handleTreeSelect(selectedKeys, info) {
      console.log("selected", selectedKeys, info);
      if (info.selected) {
        // Use route name (recommended)
        this.$router.push({ name: info.node.dataRef.key });

        // OR: Use path-based navigation
        // this.$router.push({ path: '/' + info.node.dataRef.key });
      }
    },
    addButton() {
      this.formData.buttons.push({ name: "", identifier: "", i18n: "" });
    },
    removeButton(index) {
      this.formData.buttons.splice(index, 1);
    },
    handleSelectIcon() {
      console.log("Open icon selection modal");
    },
    handleClearIcon() {
      this.formData.menuIcon = "";
    },
    handleSubmit() {
      console.log("Form data:", this.formData);
      alert("Form Submitted, check console");
    },
    onSearch(value) {
      console.log("Search value: ", value);
      // 过滤treeData
      this.treeData = this.treeData.filter(item => {
        return item.title.indexOf(value) > -1;
      });

    },

    expandAllNodes() {
      this.expandAllByDefault = true;
      this.expandedKeys = this.getAllKeys(this.treeData); // Expand all
    },
    collapseAllNodes() {
      this.expandAllByDefault = false;
      this.expandedKeys = []; // Collapse all
    },
    getAllKeys(tree) {
      let keys = [];
      for (const node of tree) {
        keys.push(node.key);
        if (node.children) {
          keys = keys.concat(this.getAllKeys(node.children));
        }
      }
      return keys;
    },
    transformRoutesToTree(routes) {
      return routes.map(route => {
        const node = {
          title: route.meta?.title || route.name,
          key: route.path,
          // icon: route.meta?.icon,
        };

        if (route.children && route.children.length) {
          node.children = this.transformRoutesToTree(route.children);
        }

        return node;
      });
    },
    handleAdd(key) {
      // 获取当前节点信息
      const parentNode = this.findNodeByKey(this.treeData, key);
      
      // 生成新节点数据
      const newNode = {
        title: "新建菜单",
        key: `${key}/new-${Date.now()}`,
        children: []
      };
      
      // 如果父节点没有children属性，添加一个空数组
      if (!parentNode.children) {
        this.$set(parentNode, 'children', []);
      }
      
      // 添加新节点
      parentNode.children.push(newNode);
      
      // 展开父节点
      if (!this.expandedKeys.includes(key)) {
        this.expandedKeys.push(key);
      }
      
      // 显示成功提示
      this.$message.success('添加成功');
    },
    
    handleEdit(key) {
      // 获取当前节点
      const currentNode = this.findNodeByKey(this.treeData, key);
      
      // 模拟加载菜单数据
      const menuData = { ...this.mockMenuData.menu };
      menuData.menuName = currentNode.title;
      menuData.routePath = currentNode.key;
      
      // 更新表单数据
      this.formData = menuData;
      
      // 显示成功提示
      this.$message.success('已加载菜单数据');
    },
    
    handleDelete(key) {
      this.$confirm({
        title: '确认删除',
        content: '确定要删除此菜单吗？此操作将同时删除其下所有子菜单。',
        okText: '确认',
        cancelText: '取消',
        onOk: () => {
          // 删除节点
          this.deleteNodeByKey(this.treeData, key);
          
          // 显示成功提示
          this.$message.success('删除成功');

          // 清除数据
          this.formData = {
            menuName: "",
            parentMenu: "权限管理",
            menuIcon: "",
            menuType: "menu",
            routePath: "",
            viewPath: "",
            viewPathPrefix: "src/modules/",
            viewPathSuffix: ".vue",
            redirectPath: "",
            i18nKey: "",
            isEnabled: true,
            isHidden: false,
            isCached: true,
            showCopyright: false,
            showBreadcrumb: true,
            isTabFixed: false,
            sortOrder: 0,
            remark: "",
            buttons: [],
          };
        }
      });
    },
    
    // 根据key查找节点
    findNodeByKey(tree, targetKey) {
      for (let node of tree) {
        if (node.key === targetKey) {
          return node;
        }
        if (node.children) {
          const found = this.findNodeByKey(node.children, targetKey);
          if (found) return found;
        }
      }
      return null;
    },
    
    // 根据key删除节点
    deleteNodeByKey(tree, targetKey, parent = null) {
      for (let i = 0; i < tree.length; i++) {
        const node = tree[i];
        if (node.key === targetKey) {
          tree.splice(i, 1);
          return true;
        }
        if (node.children) {
          const deleted = this.deleteNodeByKey(node.children, targetKey, node);
          if (deleted) {
            // 如果子节点为空，删除children属性
            if (node.children.length === 0) {
              this.$delete(node, 'children');
            }
            return true;
          }
        }
      }
      return false;
    },
    
    // 生成唯一的路由路径
    generateRoutePath(parentKey, title) {
      const slug = title
        .toLowerCase()
        .replace(/\s+/g, '-')
        .replace(/[^\w-]/g, '');
      return `${parentKey}/${slug}-${Date.now()}`;
    }
  },
};
</script>

